export default {
    data() {
        return {};
    },
    methods: {
        async handle(e) {
            const { type } = e.target.dataset;
            switch (type) {
                case "exit":

                    await this.$http("/user-service/logout", "GET");
                    await this.$store.commit("LOGOUT");

                    uni.navigateBack({});
                    break;

                case "exchange":
                    uni.navigateTo({
                        url: "../login/login"
                    });
                    break;
                case "clean":

                    await this.$http("/user-service/logout-all", "GET");
                    await this.$store.commit("LOGOUT");

                    uni.navigateBack({});
                    break;
                default:
                    break;
            }
        }
    }
};
